import React, { useState, useEffect } from 'react'
import { Collapse, Image, Button, Space } from 'antd'

export default function Images() {
  const [visible, setVisible] = useState(false)
  const [visible2, setVisible2] = useState(false)
  const [random, setRandom] = useState()
  useEffect(() => {
    setRandom(Date.now())
  }, [])

  return (
    <div>
      <h2 className='h2Style'>Image组件示例</h2>

      <Collapse defaultActiveKey={['1']} ghost expandIconPosition='end'>
        <Collapse.Panel header='图片展示状态' key='1'>
          <Space size={50}>
            <Image
              width={200}
              height={200}
              src='error'
              fallback='data:image/png;base64,'
              alt='111'
            />
            <Image width={200} preview={false} src='https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png' />
            <Image
              width={200}
              preview={false}
              src={`https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png?${random}`}
              placeholder={
                <Image
                  preview={false}
                  src='https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png?x-oss-process=image/blur,r_50,s_50/quality,q_1/resize,m_mfit,h_200,w_200'
                  width={200}
                />
              }
            />
          </Space>
        </Collapse.Panel>
      </Collapse>

      <Collapse defaultActiveKey={['1']} ghost expandIconPosition='end'>
        <Collapse.Panel header='图片预览' key='1'>
          <Space size={50}>
            <Image width={200} src='https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png' />
            <Button type='primary' onClick={() => setVisible(true)}>
              点击预览图片
            </Button>

            <Image
              width={200}
              src='https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png?x-oss-process=image/blur,r_50,s_50/quality,q_1/resize,m_mfit,h_200,w_200'
              preview={{
                src: 'https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png'
              }}
            />
          </Space>
        </Collapse.Panel>
      </Collapse>

      <Collapse defaultActiveKey={['1']} ghost expandIconPosition='end'>
        <Collapse.Panel header='多张图片预览' key='1'>
          <Space size={50}>
            <Image
              preview={{ visible: false }}
              width={200}
              src='https://gw.alipayobjects.com/zos/antfincdn/LlvErxo8H9/photo-1503185912284-5271ff81b9a8.webp'
              onClick={() => setVisible2(true)}
            />
            <div style={{ display: 'none' }}>
              <Image.PreviewGroup preview={{ visible: visible2, onVisibleChange: vis => setVisible2(vis) }}>
                <Image src='https://gw.alipayobjects.com/zos/antfincdn/LlvErxo8H9/photo-1503185912284-5271ff81b9a8.webp' />
                <Image src='https://gw.alipayobjects.com/zos/antfincdn/cV16ZqzMjW/photo-1473091540282-9b846e7965e3.webp' />
                <Image src='https://gw.alipayobjects.com/zos/antfincdn/x43I27A55%26/photo-1438109491414-7198515b166b.webp' />
              </Image.PreviewGroup>
            </div>
            <Image.PreviewGroup>
              <Image width={200} src='https://gw.alipayobjects.com/zos/rmsportal/KDpgvguMpGfqaHPjicRK.svg' />
              <Image width={200} src='https://gw.alipayobjects.com/zos/antfincdn/cV16ZqzMjW/photo-1473091540282-9b846e7965e3.webp' />
              <Image width={200} src='https://gw.alipayobjects.com/zos/antfincdn/aPkFc8Sj7n/method-draw-image.svg' />
            </Image.PreviewGroup>
          </Space>
        </Collapse.Panel>
      </Collapse>

      {/* ------------------------------------- */}

      <Image
        width={200}
        style={{ display: 'none' }}
        src='https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png?x-oss-process=image/blur,r_50,s_50/quality,q_1/resize,m_mfit,h_200,w_200'
        preview={{
          visible,
          src: 'https://gw.alipayobjects.com/zos/antfincdn/LlvErxo8H9/photo-1503185912284-5271ff81b9a8.webp',
          onVisibleChange: value => {
            setVisible(value)
          }
        }}
      />
    </div>
  )
}
